export const mutations = {
    // 查看密码
    switching(state, key) {
        // console.log(state[key].icon);

        let eye = state[key].icon === 'closed-eye'
        // if(state.password1.icon === 'closed-eye') {
        //     state.password1.icon = 'eye-o'
        // } else { 
        //     state.password1.icon = 'closed-eye'
        // }
           
        state[key].icon = eye ? 'eye-o' : 'closed-eye'
        state[key].type = eye ? 'text' : 'password';
    },

    //点击注册
    straining(state, pass) {
        state.isLoad = pass;
    },

    // 验证用户名
    validUsername(state, isProhibit) {
        // console.log('isProhibit :', isProhibit)
        state.userName.error = isProhibit ? '' : '用户名支持4-8个(字母数字_)组合，必须字母开头';
    },

    // 验证密码
    validPwd(state, isProhibit) {
        // console.log('isProhibit :', isProhibit)
        state.password1.error = isProhibit ? '' : '密码支持6-16个(字母数字_)组合，必须含有大小写字母';
    },
    // 验证用户名和密码
    veriUserPass(state, o) {
        // console.log('isProhibit :', o.isProhibit)
        state[o.key].error = o.isProhibit ? '' : o.error;
    },

    // 验证两个密码是否相等
    validEqual(state, isProhibit) {
        state.password2.error = isProhibit ? '' : '两次密码不一致';
    },

    // 用户名和密码输入是否正确
    adopt(state) {
        state.isProhibit = (state.userName.error == '' && state.userName.value != '') && (state.password1.error == '' && state.password1.value != '') && (state.password2.error == '' && state.password2.value != '') && (state.password1.value == state.password2.value);
    }
}