export const mutations = {
    // 查看密码
    switching(state, key) {
        // console.log(state[key].icon);

        let eye = state[key].icon === 'closed-eye'
       
        state[key].icon = eye ? 'eye-o' : 'closed-eye'
        state[key].type = eye ? 'text' : 'password';
    },

    //点击登录
    login(state, pass) {
        state.isLoad = pass;
    },
    // 验证用户名和密码
    veriUserPass(state, o) {
        // console.log('isProhibit :', o.isProhibit)
        state[o.key].error = o.isProhibit ? '' : o.error;
    },

    // 用户名和密码输入是否正确
    adopt(state) {
        state.isProhibit = (state.userName.error == '' && state.userName.value != '') && (state.password.error == '' && state.password.value != '');
    }
}